import $ from "$";
import styles from "./css/app.css";
import "./css/normalize.css";

let MessageBox = React.createClass({
    propTypes: {
        name: React.PropTypes.array.isRequired
    },
    getDefaultProps: function(){
      console.log(React.PropTypes.array.isRequired)
        return {
            name: "world"
        } 
    },
    getInitialState: function() {
        return {
            isVisible: true,
            // subMessages: [
            //     "我会代码搬砖",
            //     "以及花式搬砖",
            //     "不说了，工头叫我回去搬砖了。。。"
            // ],
            subMessages: "a"
        }
    },
    render: function() {
        let styleObj = {
            display: this.state.isVisible ? "block" : "none",
        }
        return (
            <div>
                <h1 style = { styleObj }>你好，{this.props.name}</h1>
                <SubMessages subMessages={ this.state.subMessages }/>
            </div>
        )
    }
})

let SubMessages = React.createClass({
    propTypes: {
        subMessages: React.PropTypes.array.isRequired
    },
    getDefaultProps: function(){
        return {
            subMessages: ["没有消息"]
        }
    },
    render: function() {
        let subMessages = []
        // this.props.subMessages.forEach(function(message, index) {
        //     subMessages.push(
        //         <p>码农说：{message}</p>
        //     )
        // })
        return (
            <div>{this.props.subMessages[0]}</div>
        )
    }
})



ReactDOM.render( <MessageBox name="galen" /> ,
    document.getElementById("app")
)



